import { Form, Input, Button, Spin, message } from 'antd';
import React from 'react';
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import animation from '../../assets/Animations/resetPassword.png';
import axios from 'axios';
import { useState } from 'react';

const ResetPassword = () => {
  const [spin, setSpin] = useState();
  const [form] = Form.useForm();

  const onFinish = (values) => {
    console.log(values);
    setSpin(true);
    axios
      .put(`https://ginnovation-server.herokuapp.com/api/resetpassword`, {
        email: values.email,
        password: values.password,
      })
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Password change Successful');
        } else {
          message.warning('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        message.error("Ouch! There's a error");
        console.log(err.response);
        // message.error(
        //   err.response.data.status === 'invalid credentials'
        //     ? 'Invalid Credentials! Check your email and password'
        //     : 'Something went wrong!'
        // );
      });
  };

  return (
    <div className='forget-password-page reset-password-page'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='container'>
        <div className='image-container'>
          <img src={animation} alt='forgot password' />
        </div>
        <header>
          <h2>Reset Password</h2>
          <p>No worries, you can reset it by entering your email below</p>
          <div className='input-div'>
            <Form
              form={form}
              className='my-form'
              name='resetPassword'
              onFinish={onFinish}
            >
              <Form.Item
                name='email'
                rules={[
                  {
                    required: true,
                    message: 'Please input your email!',
                  },
                ]}
              >
                <Input
                  prefix={<UserOutlined className='site-form-item-icon' />}
                  placeholder='Your Email'
                />
              </Form.Item>
              <Form.Item
                name='password'
                rules={[
                  {
                    required: true,
                    message: 'Please input your password!',
                  },
                ]}
                hasFeedback
              >
                <Input.Password
                  prefix={<LockOutlined className='site-form-item-icon' />}
                  placeholder='Password'
                />
              </Form.Item>
              <Form.Item
                name='confirm'
                dependencies={['password']}
                hasFeedback
                rules={[
                  {
                    required: true,
                    message: 'Please confirm your password!',
                  },
                  ({ getFieldValue }) => ({
                    validator(rule, value) {
                      if (!value || getFieldValue('password') === value) {
                        return Promise.resolve();
                      }

                      return Promise.reject(
                        'The two passwords that you entered do not match!'
                      );
                    },
                  }),
                ]}
              >
                <Input.Password
                  prefix={<LockOutlined className='site-form-item-icon' />}
                  placeholder='Confirm Password'
                />
              </Form.Item>

              <div className='form-btn-row'>
                <div className='register-btn-container'>
                  <Button type='primary' htmlType='submit'>
                    Submit
                  </Button>
                </div>
              </div>
            </Form>
          </div>
        </header>
      </div>
    </div>
  );
};

export default ResetPassword;
